import {
    primaryColor,
    dangerColor,
    successColor,
    defaultFont,
    whiteColor,
    grayColor,
    textbox
  } from "../../material-dashboard-pro-react";
  
  const customInputStyle = {
    disabled: {
      "&:before": {
        borderColor: "transparent !important"
      }
    },
    underline: {
      "&:hover:not($disabled):before,&:before": {
        borderColor: grayColor[4] + "!important",
        borderWidth: "1px !important"
      },
      "&:after": {
        borderColor: "#57adc2"
      },
      "& + p": {
        fontWeight: "300"
      }
    },
    Mandatory: {
      "&:hover:not($disabled):before,&:before": {
        borderColor: "red !important",
        borderWidth: "1px !important"
      },
      "&:after": {
        borderColor: 'red'
      },
      "& + p": {
        fontWeight: "300"
      }
    },
    underlineError: {
      "&:after": {
        borderColor: dangerColor[0]
      }
    },
    underlineSuccess: {
      "&:after": {
        borderColor: successColor[0]
      }
    },
    labelRoot: {
      ...defaultFont,
      color: textbox + " !important",  // change blue default color 
      fontWeight: "400",
      fontSize: "14px",
      lineHeight: "1.42857",
      top: "10px",
      letterSpacing: "unset",
      "& + $underline": {
        marginTop: "0px"
      },
      "& + $Mandatory": {
        marginTop: "0px"
      }
    },
    mandatoryLabelRoot: {
      ...defaultFont,
      color: "red !important",
      fontWeight: "400",
      fontSize: "14px",
      lineHeight: "1.42857",
      top: "10px",
      letterSpacing: "unset",
      "& + $Mandatory": {
        marginTop: "0px"
      }
    },
    labelRootError: {
      color: dangerColor[0] + " !important"
    },
    labelRootSuccess: {
      color: successColor[0] + " !important"
    },
    formControl: {
      margin: "-15px 0 17px 0",
      paddingTop: "27px",
      position: "relative",
      verticalAlign: "unset",
      "& svg,& .fab,& .far,& .fal,& .fas,& .material-icons": {
        color: grayColor[14]
      }
    },
    whiteUnderline: {
      "&:hover:not($disabled):before,&:before": {
        backgroundColor: whiteColor
      },
      "&:after": {
        backgroundColor: whiteColor
      }
    },
    input: {
      color: grayColor[14],
      height: "unset",
      "&,&::placeholder": {
        fontSize: "14px",
        fontFamily: '"Roboto", "Helvetica", "Arial", sans-serif' ,
        fontWeight: "400",
        lineHeight: "1.42857",
        opacity: "1"
      },
      "&::placeholder": {
        color: grayColor[3]
      }
    },  
    whiteInput: {
      "&,&::placeholder": {
        color: whiteColor,
        opacity: "1"
      }
    }
  };
  
  export default customInputStyle;
  